import React, { useEffect, useState } from 'react';
import type { AppProps } from 'next/app';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Box from '@mui/material/Box';

// Import the new BottomBar component
import BottomBar from '../components/BottomBar';

// Import version.json and mainController
import versionInfo from '../conf/version.json';
import { mainController } from '../libs/mc/main/MainController';

// Create a default MUI theme
const theme = createTheme({
  palette: {
    mode: 'light',
  },
});

function MyApp({ Component, pageProps }: AppProps) {
  const [isClient, setIsClient] = useState(false);
  async function init() {
    await mainController.init();

    // On client-side load, print version information
    if (versionInfo && versionInfo.version && versionInfo.buildTime) {
      const buildDate = new Date(parseInt(versionInfo.buildTime, 10));
      console.log(`App Version: ${versionInfo.version}`);
      console.log(`Build Time: ${buildDate.toLocaleString()}`);
    } else {
      console.warn('version.json not found or incomplete.');
    }
    setIsClient(true);
  }

  useEffect(() => {
    init();
  }, []);

  if (!isClient) {
    return null;
  }
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      {/* Add padding to the bottom to prevent content from being obscured by the BottomBar */}
      <Box sx={{ pb: '56px' }}>
        <Component {...pageProps} />
      </Box>
      <BottomBar />
    </ThemeProvider>
  );
}

export default MyApp;
